/** Web Worker Service
 * ************************************/
import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class SignalKStreamWorker  {

	private _error: Subject<any>;
	private _message: Subject<any>;
    private worker: Worker;
    
   // **************** ATTRIBUTES ***************************
	
    public onError: Observable<any>; 	
    public onMessage: Observable<any>;	

   // *******************************************************    

    constructor() {
        this._error= new Subject<string>();
        this.onError= this._error.asObservable();  
        this._message= new Subject<string>();
        this.onMessage= this._message.asObservable();   
    } 

    ngOnDestroy() { this.worker.terminate(); this.worker = undefined; }

    // ** Initialise worker
    init(pathToFile:string) { 
        if(typeof(Worker)== "undefined") { return false }
        if(this.worker) { this.worker.terminate() }   // ** terminate an open worker

        this.worker= new Worker(pathToFile);
        this.worker.onmessage= event=> { this._message.next(event) };
        this.worker.onerror= event=> { this._error.next(event) };           
        // ** worker ready for postMessage()
    }    
    
    // ** Send message to worker
    postMessage(msg:any) { if(this.worker) {this.worker.postMessage(msg) } }

    // ** terminate worker
    terminate() { if(this.worker) {this.worker.terminate()} }
}