import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { Message, Alarm, AlarmType } from './utils';

@Injectable({ providedIn: 'root' })
export class SignalKStream {

	private _connect: Subject<any>;
    private _close: Subject<any>;
	private _error: Subject<any>;
    private _message: Subject<any>;

    private ws: any;    
    private _filter= null;               // ** id of vessel to filter delta messages
    private _wsTimeout= 20000;           // ** websocket connection timeout  
    private _token: string;  
    private _playbackMode: boolean= false;
    
   // **************** ATTRIBUTES ***************************

    public onConnect: Observable<any>;		
    public onClose: Observable<any>;	
    public onError: Observable<any>; 	
    public onMessage: Observable<any>;

    public endpoint: string;
    public selfId: string;
    public _source: any= null;

    // ** set source label for use in messages
    set source(val:string) { 
        if(!this._source) { this._source= {} }
        this._source['label']= val;
    }

    // ** set auth token value **
    set token(val:string) { this._token= val }    
    // ** get / set websocket connection timeout 3000<=timeout<=60000 **
    get connectionTimeout():number { return this._wsTimeout }
    set connectionTimeout(val:number) {
        this._wsTimeout= (val<3000) ? 3000 : (val>60000) ? 60000 : val;
    }   
    // ** is WS Stream connected?
    get isOpen():boolean { 
        return (this.ws && (this.ws.readyState!=1 && this.ws.readyState!=3) ) ? true : false;
    }  
    // ** get / set filter to select delta messages just for supplied vessel id   
    get filter():string { return this._filter }
    // ** set filter= null to remove message filtering
    set filter(id:string) { 
        if( id && id.indexOf('self')!=-1 ) {  // ** self
            this._filter= (this.selfId) ? this.selfId : null;
        }
        else { this._filter= id }
    }   
    // ** returns true if Playback Hello message
    get playbackMode():boolean { return this._playbackMode }

    // ******************************************************

    constructor( ) { 
        this._connect= new Subject<any>();
        this.onConnect= this._connect.asObservable();   
        this._close= new Subject<any>();
        this.onClose= this._close.asObservable();  
        this._error= new Subject<any>();
        this.onError= this._error.asObservable();  
        this._message= new Subject<any>();
        this.onMessage= this._message.asObservable();         
    }   

    // ** Close WebSocket connection
    close() { if(this.ws) { this.ws.close(); this.ws= null; } }
   
	// ** Open a WebSocket at provided url
	open(url:string, subscribe?:string, token?:string) {
        url= (url) ? url : this.endpoint;
        if(!url) { return }
        let q= (url.indexOf('?')==-1) ? '?' : '&'
        if(subscribe) { url+=`${q}subscribe=${subscribe}` } 
        if(this._token || token) { url+= `${(subscribe) ? '&' : '?'}token=${this._token || token}` } 
        
        this.close();
        this.ws = new WebSocket(url);
        // ** start connection watchdog **
        setTimeout( 
            ()=>{
                if(this.ws && (this.ws.readyState!=1 && this.ws.readyState!=3) ) { 
                    console.warn(`Connection watchdog expired (${this._wsTimeout/1000} sec): ${this.ws.readyState}... aborting connection...`);
                    this.close(); 
                }
            }, this._wsTimeout
        );
		
		this.ws.onopen= e=> { this._connect.next(e) }
		this.ws.onclose= e=> { this._close.next(e) }
		this.ws.onerror= e=> { this._error.next(e) }
		this.ws.onmessage= e=> {this.parseOnMessage(e) }
    }  
    
    // ** parse received message
    parseOnMessage(e) {
        let data: any;
        if(typeof e.data === 'string') {
            try { data= JSON.parse(e.data) }
            catch(e) { return }
        }
        if(this.isHello(data)) { 
            this.selfId= data.self;
            this._playbackMode= (typeof data.startTime!= 'undefined') ? true : false;                 
            this._message.next(data);
        }          
        else if(this._filter && this.isDelta(data)) {
            if(data.context== this._filter) { this._message.next(data) } 
        }                
        else { this._message.next(data) } 
    }

    // ** send data to Signal K stream
    send(data:any) {
        if(this.ws) {
            if(typeof data === 'object') { data= JSON.stringify(data) }
            this.ws.send(data);
        }
    }

    // ** send value(s) via delta stream update **
    sendUpdate(context:string, path:Array<any>);
    sendUpdate(context:string, path:string, value:any);
    sendUpdate(context:string='self', path: string | Array<any>, value?:any) {
        let val= Message.updates();
        val.context= (context=='self') ? 'vessels.self' : context;
        if(this._token) { val['token']= this._token }

        let uValues= [];
        if(typeof path === 'string') {
            uValues.push({ path: path, value: value });
        }
        if(typeof path === 'object' && Array.isArray(path) ) {
            uValues= path;
        }
        let u= { 
            timestamp: new Date().toISOString(), 
            values: uValues 
        }
        if(this._source) { u['source']= this._source }
        val.updates.push( u ); 
        this.send(val);
    }

    // ** Subscribe to Delta stream messages options: {..}**
    subscribe(context:string, path:Array<any>);
    subscribe(context:string, path:string, options?:any);
    subscribe(context:string='*', path:string | Array<any>='*', options?:any) {
        let val= Message.subscribe();
        val.context= (context=='self') ? 'vessels.self' : context;
        if(this._token) { val['token']= this._token }

        if(typeof path === 'object' && Array.isArray(path) ) {
           val.subscribe= path;
        }
        if(typeof path === 'string') {
            let sValue= {};
            sValue['path']= path;
            if(options && typeof options === 'object') {
                if(options['period']) { sValue['period']= options['period'] }
                if(options['minPeriod']) { sValue['minPeriod']= options['period'] }
                if(options['format'] && 
                    (options['format']=='delta' || options['format']=='full') ) { 
                        sValue['format']= options['format'];
                }
                if(options['policy'] && 
                    (options['policy']=='instant' || options['policy']=='ideal'
                    || options['policy']=='fixed') ) { 
                        sValue['policy']= options['policy'];
                }
            }
            val.subscribe.push(sValue);
        }
        this.send(val);        
    }

    // ** Unsubscribe from Delta stream messages **
    unsubscribe(context:string='*', path:any='*') {
        let val= Message.unsubscribe();
        val.context= (context=='self') ? 'vessels.self' : context;
        if(this._token) { val['token']= this._token }

        if(typeof path === 'object' && Array.isArray(path) ) {
           val.unsubscribe= path;
        }
        if(typeof path === 'string') { val.unsubscribe.push({path: path}) }
        this.send(val); 
    }

    // ** raise alarm for path
    raiseAlarm(context:string, name:string, alarm:Alarm);
    raiseAlarm(context:string, type:AlarmType, alarm:Alarm);
    raiseAlarm(context:string='*', alarmId:any, alarm:Alarm) {
        let n:string;
        if(typeof alarmId === 'string') {
            n=(alarmId.indexOf('notifications.')==-1) ? `notifications.${alarmId}` : alarmId;
        }
        else { n= alarmId }
        this.sendUpdate(context, n, alarm.value );
    }

    // ** raise alarm for path
    clearAlarm(context:string='*', name:string) {
        let n=(name.indexOf('notifications.')==-1) ? `notifications.${name}` : name;
        this.sendUpdate(context, n, null);
    }    

    // *************** MESSAGE PARSING ******************************
    // ** returns true if message context is 'self'
    isSelf(msg:any):boolean { return (msg.context== this.selfId) }
    // ** returns true if message is a Delta message
    isDelta(msg:any):boolean { return typeof msg.context!= 'undefined' }
    // ** returns true if message is a Hello message
    isHello(msg:any):boolean { 
        return (typeof msg.version!= 'undefined' && typeof msg.self!= 'undefined');
    }     
    // ** returns true if message is a request Response message
    isResponse(msg:any):boolean { return typeof msg.requestId!= 'undefined' } 
}